.error {
    border: 2px solid red;
}
.forControl {
    position: relative;
}
.errorMessage {
    color: red;
}

.loginError {
    color: red;
    border: 1px solid red;
    display: inline-block;
    padding: 5px 10px;
}
.loginForm {
    margin: 25px;
}
label.tab {
    border-radius: 3px 3px 0 0;
    margin-bottom: -1px;
    cursor: pointer;
    color: #ffffff;
    font-weight: 600;
    padding: 15px;
    display: inline-block;
}
label.tab.active {
    background: #fff;
    color: #1b5f75;
}